.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#root > div > div.container > div > div.col-10 > div > div > section > div > div:nth-child(1) > div > a > button > i,
#root > div > div.container > div > div.col-10 > div > div > section > div > div:nth-child(2) > div > a > button > i {
  padding-right:0.3rem;
}


.loading-container {
  margin: 0 auto;
  
}


.picture-box {
  /* width: 400px; */
  /* height: 200px; */
  max-width: 100%;
  background: var(--bg2);
  margin: 1rem auto;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
}

.picture-box-border {
  border: 20px solid;
  border-image-source: url(/images/gallery-8.jpg);
  border-image-slice: 60 30;
}